.icon-round {
  @include currentColorOptions;

  width: 4rem;
  height: 4rem;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 1.5rem;
  color: var(--current-color);
  border: 1px solid currentColor;

  &--no-border {
    border: none;
  }
}
